import React from "react";
import styled from "styled-components";
import { getWeek } from "../hook/getDate";
import Month from "./month/Month";

interface Props {
  date: string;
}

const CalendarBlock = styled.div`
  display: grid;
  grid-template-columns: 50px auto;
  grid-template-areas:
    ". a"
    ". b";
  width: 100%;
  gap: 5px;
  border-bottom: var(--border-block);
  border-top: var(--border-block);
  background-color: var(--grey-background);
  @media (max-width: 568px) {
    grid-template-columns: 40px auto;
  }
`;

const WidthBlock = styled.div`
  display: flex;
  justify-content: space-between;
  grid-area: a;
  gap: 5px;
`;

const DateBlock = styled.div`
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 5px;
  width: 100%;
  justify-content: center;
`;

const DayNumber = styled.button<Props>`
  font-size: 25px;
  width: min-content;
  border-radius: 45px;
  padding: 5px;
  background: ${(p: Props) =>
    p.date === new Date().toLocaleString("ru-RU", { dateStyle: "short" })
      ? "red"
      : "none"};
  color: ${(p: Props) =>
    p.date === new Date().toLocaleString("ru-RU", { dateStyle: "short" })
      ? "white"
      : "none"};
  border: none;
  cursor: default;
  @media (max-width: 480px) {
    font-size: 18px;
  }
`;

const Calendar = () => {
  return (
    <CalendarBlock>
      <WidthBlock>
        {getWeek().map((number) => (
          <DateBlock key={number.toLocaleString()}>
            <div>
              {number
                .toLocaleString("en-US", {
                  weekday: "short",
                })
                .slice(0, -2)}
            </div>
            <DayNumber
              key={number.toLocaleString()}
              type="button"
              date={number.toLocaleString("ru-RU", { dateStyle: "short" })}
            >
              {number.toLocaleString("en-US", {
                day: "numeric",
              })}
            </DayNumber>
          </DateBlock>
        ))}
      </WidthBlock>
      <Month
        date={getWeek()[0].toLocaleString("ru-RU", {
          month: "long",
          year: "numeric",
        })}
      />
    </CalendarBlock>
  );
};

export default Calendar;
